.orderFeed {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 0;
  gap: 16px;
  list-style: none;
  max-height: 835px;
  overflow-y: auto;
}

.digit__effect {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25), 0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
  text-align: center;
}

.orderTitle {
  font-family: 'Inter';
  margin-top: 100px;
}

.orders {
  width: 844px;
}   