.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  max-height: 718px;
  max-width: 720px;
  width: 100%;
  border-radius: 40px;
  background-color: #1C1C21;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
}

.modal__close_icon {
  position: absolute;
  top: 50px;
  right: 40px;
  cursor: pointer;
}