.loginContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 40px;
}

.list {
  list-style: none;
  text-align: start;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  margin: 0;
  padding-top: 80px;
}

.list__item {
  display: flex;
  align-items: center;
  cursor: pointer;
  width: 100%;
  height: 64px;
  margin: 0;
  text-decoration: none;
}

.list__button {
  text-align: start;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: all .2s linear;
}

.login_signupContainer {
  display: flex;
  justify-content: center;
}

.list__span {
  display: inline-block;
  width: 100%;
  margin-top: 80px;
}

.profile__button {
  align-self: flex-end;
}

.active {
  color: white;
}
